<template>
  <loading :active="isLoading"></loading>
  <div class="d-flex justify-content-between ">
    <h2><i class="bi bi-cup-hot-fill"></i> 產品列表</h2>
    <button
      class="btn btn-primary text-end"
      type="button"
      @click="openModal(true)"
    >
      新增產品
    </button>
  </div>
  <table class="table mt-4 text-center border">
    <thead class="table-dark">
      <tr>
        <th>分類</th>
        <th>產品名稱</th>
        <th>原價</th>
        <th>售價</th>
        <th>上架</th>
        <th class="text-center">操作</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in products" key="item.id">
        <td>{{ item.category }}</td>
        <td>{{ item.title }}</td>
        <td>{{ $filters.currency(item.origin_price) }}</td>
        <td>{{ $filters.currency(item.price) }}</td>
        <td>
          <span class="text-success" v-if="item.is_enabled">啟用</span>
          <span class="text-danger" v-else>未啟用</span>
        </td>
        <td class="text-center">
          <button
            class="btn btn-outline-success"
            @click="openModal(false, item)"
          >
            編輯
          </button>
          <button class="btn btn-outline-danger" @click="openDelModal(item)">
            刪除
          </button>
        </td>
      </tr>
    </tbody>
  </table>
  <Pagination :pages="pagination" @emit-pages="getProducts"></Pagination>
  <ProductModal
    ref="productModal"
    :product="tempProduct"
    @update-product="updateProduct"
  ></ProductModal>
  <DelModal ref="delModal" :item="tempProduct" @del-item="delProduct">
  </DelModal>
</template>
<script>
import ProductModal from "@/components/back/ProductModal.vue";
import DelModal from "@/components/back/DelModal.vue";
import Pagination from "@/components/layout/Pagination.vue";
export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      message: [],
      isNew: false,
      isLoading: false,
    };
  },
  inject: ["emitter", "$httpMessageState"],
  components: {
    ProductModal,
    DelModal,
    Pagination,
  },
  methods: {
    getProducts(page = 1) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.isLoading = false;
          console.log(res);
          this.products = res.data.products;
          this.pagination = res.data.pagination;
        }
      });
    },
    openModal(isNew, item) {
      // console.log(isNew,item);
      if (isNew) {
        this.tempProduct = {};
      } else {
        this.tempProduct = { ...item };
      }
      this.isNew = isNew;
      const productComponent = this.$refs.productModal;
      productComponent.showModal();
    },
    updateProduct(item) {
      this.isLoading = true;
      this.tempProduct = item;
      // 新增
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`;
      let httpMethod = "post";

      // 編輯
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`;
        httpMethod = "put";
      }
      const productComponent = this.$refs.productModal;
      this.$http[httpMethod](api, { data: this.tempProduct }).then((res) => {
        this.isLoading = false;
        console.log(res);
        productComponent.hideModal();
        this.message = res.data.message;
        console.log(this.message);
        this.$httpMessageState(res);
        this.getProducts();
      });
    },
    openDelModal(item) {
      this.tempProduct = { ...item };
      const delComponent = this.$refs.delModal;
      delComponent.showModal();
    },
    delProduct() {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
      this.$http
        .delete(api)
        .then((res) => {
          console.log(res);
          const delComponent = this.$refs.delModal;
          this.message = res.data.message;
          this.$httpMessageState(res);
          delComponent.hideModal();
          this.getProducts();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
